:root {
  --main-color: #050505;
  --main-rgb-color: #050505;
  --secondary-color: #f8f8f8;
  --secondary-rgb-color: #f8f8f8;
  --active-color: #9bb57;
  --accent-color: #eea10c;
  --additional-color: #cecdcd;
  --rare-color: rgba(160, 158, 160, .6);
  --box-shadow: 7px 5px 15px 0px var(--rare-color);
  --transition-duration: 250ms;
  --transition-time-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  overflow-x: hidden;
  overflow-y: scroll;
}

@media screen and (min-width: 374px) {
  .container {
    padding: 27.5px 20px 80px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    padding: 32px 32px 100px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    padding: 40px 100px 100px;
  }
}

